.app {
    width: 100vw;

    main {
        height: calc(100vh - 44px);
    }
}

.main-layout {
    display: grid;
    grid-template-columns: minmax(40px, 7%) 1fr minmax(40px, 7%);

    // grid-template-columns: minmax(40px,10%) 1fr minmax(40px,10%);
    @include for-mobile-layout {
        grid-template-columns: 5% 1fr 5%;
    }

    >* {
        grid-column: 2;

        &.full {
            grid-column: 1 /-1;
        }
    }
}