.watcher-details {
    background-color: $clr3;
    position: relative;

    >* {
        padding: 0 5%;
    }

    &-header {
        background-color: rgb(20 20 20);
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        nav {
            a {
                color: $clr2;
                margin-inline-end: 10px;

                &.active {
                    color: $clr1;
                }
            }
        }

        .watcher-img {
            height: 85%;

            img {
                border-radius: 3px;
            }
        }
    }

    .hero-movie {
        aspect-ratio: 1/0.4;
        background-position: 50%;
        background-size: cover;
        color: $clr2;
        display: flex;
        align-items: flex-end;

        .hero-content {
            height: fit-content;
            width: min-content;

            h2 {
                font-size: 3em;
                font-weight: 500;
                margin-bottom: 20px;
                width: max-content;
            }

            @include for-mobile-layout {
                font-size: 3vw;

                h2 {
                    width: min-content;
                }
            }

            p {
                margin-bottom: 15px;
            }

            .btns-container {
                margin-bottom: 25px;
                display: flex;
                gap: 15px;

                >* {
                    display: inline-flex;
                    align-items: center;
                    gap: 10px;
                    padding: 5px 15px;
                    font-weight: 400;
                }

                .play-btn {
                    color: $clr4;
                    background-color: $clr2;
                }

                .info-btn {
                    background-color: #6d6d6eb3;
                }
            }
        }
    }

    h5 {
        font-size: 1.25em;
        color: $clr2;
        font-weight: 300;
        margin: 5px 0;
    }

    .movie-list {
        display: flex;
        align-items: center;
        column-gap: 5px;
        overflow-x: auto;
        height: 130px;

        .movie-preview {
            cursor: pointer;
            border-radius: 2px;
            height: 100%;
            aspect-ratio: 1/0.56;
            background-size: cover;

            &:hover {
                transform: scale(1.01);
            }
        }
    }

    .screen {
        position: absolute;
        top: 0;
        // height: calc(100vh - 44px);
        height: 100%;
        width: 100vw;
        padding: 0;
        background-color: rgba(0, 0, 0, 0.7);
    }

    .movie-details {
        position: absolute;
        top: 50%;
        left: 50%;
        background-color: $clr3;
        transform: translate(-50%, -50%);
        height: 70%;
        width: 90%;
        padding: 0;
        margin: 0 auto;

        @include for-mobile-layout {
            width: 100%;
            height: 40%;
        }

        iframe {
            height: 100%;
            width: 100%;

        }

    }
}