.home {
    position: relative;
    background-size: cover;
    background-position: 50%;
    padding: 20% 0;
    filter: brightness(0.9);

    .home-content {
        color: $clr2;
        width: 100%;
        text-align: center;

        >* {
            font-weight: 400;
        }

        h1 {
            font-size: 3.125rem;
        }

        h2 {
            font-size: 1.625rem;
            margin: 16px 6px;
        }

        h3 {
            font-size: 23px;
            padding: 0 10%;
        }

        button {
            background-color: $clr1;
            width: fit-content;

            &.sign-in-btn {
                margin-bottom: 50px;
            }

            &.start-btn {
                margin: 20px auto 0;

                a {
                    color: inherit;
                }
            }
        }
    }
}